var qiniu=require('qiniu-js');
import myIssue  from '../../../../components/start/App.vue'
export default{
	data(){
		return{
							avatar:['https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg','https://ossweb-img.qq.com/images/lol/web201310/skin/big81005.jpg','https://ossweb-img.qq.com/images/lol/web201310/skin/big25002.jpg','https://ossweb-img.qq.com/images/lol/web201310/skin/big99008.jpg'],
							imgList: [],
							cur1:5,
							cur2:5,
							cur3:5,
							flag:false,
							repaitItem:'',
							files:[],
							summary:'',//报修内容
							detail1:'非常满意',
							detail2:'非常满意',
							detail3:'非常满意',
							orderID:''

		}
	},
	components:{
		myIssue
	},
	methods:{
		//获得订单信息
		getOrderInfo(id){
			uni.request({
				url:this.$store.state.url+'ServiceOrder',
				data:{
					id:id
				},
				success: (res) => {
					if(!res.data.data.summary){
						res.data.data.summary=''
					}
					this.repaitItem=res.data.data;
				}
			})
		},
	ChooseImage(event) {
		uni.chooseImage({
			count: 9, //默认9
			sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			sourceType: ['album'], //从相册选择
			success: (res) => {
				console.log(res)
				if (this.imgList.length != 0) {
					this.imgList = this.imgList.concat(res.tempFilePaths)
				} else {
					this.imgList = res.tempFilePaths
				}
	
				var putExtra={
					params:{
						// 'x:owner': this.$store.state.userInfo.owner,
						// 'x:creator': this.$store.state.userInfo.id,
						// 'x:type':this.$store.state.serviceorder_file,
						'x:owner': 18,
						'x:creator': 49,
						'x:type':18,
					}
				}
	
				var config={
					 useCdnDomain: false
				}
				for(var i=0;i<res.tempFilePaths.length;i++){
					var observer=qiniu.upload(res.tempFilePaths[i], res.tempFilePaths[i].split('/')[3]+'.jpg', this.token, putExtra, config);
					var subscription = observer.subscribe(
					  ()=>{
	
					  },
					  ()=>{
	
					  },
					  (res)=>{
							this.files.push(res.data)
							if(this.files.length===this.imgList.length){
								uni.showToast({
									title:'上传成功',
									icon:'success'
								})
							}
					  }
					);
				}
	
			},
		})
	},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e
			});
		},
		DelImg(event) {
			uni.showModal({
				content: '确定删除？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(event, 1);
						uni.request({
							url:this.$store.state.url+'RemoveFiles',
							data:{
								id:this.files[event],
								owner:18
							},
							success: (res) => {
								this.files.splice(event,1)
								uni.showToast({
									title:'删除成功',
									icon:'none'
								})
							}
						})
					}
				}
			})
		},
		//获得上传token
		getUploadToken(){
		
			uni.request({
				url:this.$store.state.url+'UploadToken',
				success: (res) => {
					this.token=res.data.data
		
				}
			})
		},
		selected(n,type){
			
			this.flag=true;
			if(type=='service'){
				this.cur1=n;
				if(n>=5){
					this.detail1='非常满意';
				}else if(n>=3){
					this.detail1='满意';
				}else if(n>=1){
					this.detail1='一般';
				}else {
					this.detail1='不满意';
				}
			}
			if(type=='timely'){
				this.cur2=n;
				if(n>=5){
					this.detail2='非常满意';
				}else if(n>=3){
					this.detail3='满意';
				}else if(n>=1){
					this.detail2='一般';
				}else {
					this.detail2='不满意';
				}
			}
			if(type=='quantity'){
				this.cur3=n;
				if(n>=5){
					this.detail3='非常满意';
				}else if(n>=3){
					this.detail3='满意';
				}else if(n>=1){
					this.detail3='一般';
				}else {
					this.detail3='不满意';
				}
			}
			
		},
		createComment(){
			console.log(this.files.join(','))
			console.log(this.summary);
			console.log(this.cur1)
			console.log(this.cur2)
			console.log(this.cur3)
			uni.request({
				url:this.$store.state.url+'NewServiceOrderComments',
				data:{
					owner:18,
					userId:43,
					comment:this.summary,
					id:this.orderID,
					service:this.cur1,
					timely:this.cur2,
					quality:this.cur3,
					files:this.files?this.files.join(','):''
					
				},
				success: (res) => {
					uni.showToast({
						title:'验收评价成功',
						icon:'none'
					});
					setTimeout(()=>{
						uni.navigateBack({
							delta: 1
						});
					},500)
				}
			})
		}
	},
	onLoad(option){
		
		this.getUploadToken();
		if(option){
			this.orderID=option.orderID;
			this.getOrderInfo(option.orderID);
		}
	}
	
}
